@use 'variables';

.event {
    padding: 80px 0;


    .container {
        display: flex;
        gap: 40px;
    }

    &__image {
        max-width: 500px;
    }

    &__details {

        &__title {
            font-size: 32px;
            font-weight: bold;
            margin-bottom: 16px;
            margin-left: 8px;
        }

        &__descriptions {
            line-height: 22px;
        }
    }

    @media (max-width: 640px) {
        .container {
            display: block;
        }

        &__image {
            width: 100%;
        }

        &__details {
            margin-right: 0;
            margin-bottom: 16px;
        }
    }
}